import React from 'react';
import {Link, Router} from 'react-router-dom';
import {connect} from 'react-redux';

import {userActions} from '../redux/actions';
import TableView from './tables/TableView';
import Header from "./common/Header";
import Footer from "./common/Footer";
import LeftNavigation from "./common/LeftNavigation";
import {ToggleButton, ToggleButtonGroup} from '@material-ui/lab';
import TableChartIcon from '@material-ui/icons/TableChart';
import GridOnIcon from '@material-ui/icons/GridOn';
import CardView from "../components/cards/CardView";

class HomePage extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			view: 'grid'
		};
	}

	componentDidMount() {
		this.props.getUsers();
	}

	handleDeleteUser(id) {
		return (e) => this.props.deleteUser(id);
	}

	render() {
		const {user, users} = this.props;
		const {view} = this.state;

		return (
			<div>
				<Header/>
				<div className="body">
					<div className="view-buttons d-flex justify-content-center">
						<ToggleButtonGroup
							value={view}
							exclusive
							onChange={(e, value) => this.setState({view: value})}
							aria-label="text alignment"
						>
							<ToggleButton value="grid" aria-label="left aligned">
								<GridOnIcon/>
							</ToggleButton>
							<ToggleButton value="table" aria-label="right aligned">
								<TableChartIcon/>
							</ToggleButton>
						</ToggleButtonGroup>
					</div>

					{/*<LeftNavigation/>*/}
					<div className="container">

						{
							view === 'grid' ?
								<div className="grid">
									<CardView/>
								</div>
								: <div className="table">
									<TableView/>
								</div>
						}

					</div>
				</div>
				{/*<Footer/>*/}
			</div>
		);
	}
}

function mapState(state) {
	const {users, authentication} = state;
	const {user} = authentication;
	return {user, users};
}

const actionCreators = {
	getUsers: userActions.getAll,
	deleteUser: userActions.delete
};

export default connect(mapState, actionCreators)(HomePage);