import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import {bindActionCreators} from 'redux';

import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import {menuActions, userActions} from "../../redux/actions";
import _ from 'lodash';
import Avatar from "@material-ui/core/Avatar";

class Header extends React.Component {
	componentDidMount() {
		this.props.getUsers();
	}

	render() {

		const userName = _.get(this.props, 'userLogged.firstName') || '';
		return (
			<div className="header-component">
				<IconButton edge="start" color="inherit" aria-label="menu" onClick={() => this.props.toggleNavigation()}>
					<MenuIcon/>
				</IconButton>
				<div className="d-flex align-items-center justify-content-center">
					<Avatar aria-label="recipe" style={{marginRight: 10}}>
						{userName}
					</Avatar>
					<Typography variant="h6">
						{userName}
					</Typography>
					<div className="single-button">
						<Link to="/login" className="btn btn-link link-button border-link">Logout</Link>
					</div>
				</div>

			</div>
		);
	}
}

Header.propTypes = {
	toggleNavigation: PropTypes.func.isRequired,
	openNavigation: PropTypes.bool.isRequired,
};


const mapStateToProps = (state, props) => {
	const {authentication} = state;
	const {userLogged} = authentication;

	return ({openNavigation: state.menu.openNavigation, userLogged})
};

const mapDispatchToProps = dispatch => {
	return ({
		toggleNavigation: bindActionCreators(menuActions.toggleNavigation, dispatch),
		getUsers: userActions.getAll,
	});
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);