import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {userActions} from '../redux/actions';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

class LoginPage extends React.Component {
	constructor(props) {
		super(props);

		// reset login status
		// this.props.logout();

		this.state = {
			username: '',
			password: '',
			submitted: false
		};

		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(e) {
		const {name, value} = e.target;
		this.setState({[name]: value});
	}

	handleSubmit(e) {
		e.preventDefault();

		this.setState({submitted: true});
		const {username, password} = this.state;
		if (username && password) {
			this.props.login(username, password);
		}
	}

	render() {
		const {loggingIn} = this.props;
		const {username, password, submitted} = this.state;
		return (
			<div className="login body">
				<div className="login-form">

					<Card className='form'>
						<h2>Login</h2>
						<form name="form" onSubmit={this.handleSubmit}>
							<CardContent>
								<div className={'form-group' + (submitted && !username ? ' has-error' : '')}>
									<TextField id="username" name="username" type="text" variant="outlined" label="Username"
														 onChange={this.handleChange} fullWidth value={username}/>

									{submitted && !username &&
									<div className="help-block">Username is required</div>
									}
								</div>
								<div className={'form-group' + (submitted && !password ? ' has-error' : '')}>
									<TextField id="password" name="password" type="password" variant="outlined" label="Username"
														 onChange={this.handleChange} fullWidth value={password}/>
									{submitted && !password &&
									<div className="help-block">Password is required</div>
									}
								</div>
							</CardContent>
							<CardActions>
								<div className="button-group">
									<Button type='submit' className="btn btn-primary action-button">Login</Button>
									{loggingIn &&
									<img
										src="data:image/gif;base64,"/>
									}
									<Link to="/register" className="btn btn-link link-button">Register</Link>
								</div>
							</CardActions>
						</form>
					</Card>

				</div>
			</div>
		);
	}
}

function mapState(state) {
	const {loggingIn} = state.authentication;
	return {loggingIn};
}

const actionCreators = {
	login: userActions.login,
	logout: userActions.logout
};

export default connect(mapState, actionCreators)(LoginPage);