import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { userActions } from '../redux/actions';
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import TextField from "@material-ui/core/TextField";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";

class RegisterPage extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			user: {
				firstName: '',
				lastName: '',
				username: '',
				password: ''
			},
			submitted: false
		};

		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(event) {
		const { name, value } = event.target;
		const { user } = this.state;
		this.setState({
			user: {
				...user,
				[name]: value
			}
		});
	}

	handleSubmit(event) {
		event.preventDefault();

		this.setState({ submitted: true });
		const { user } = this.state;
		if (user.firstName && user.lastName && user.username && user.password) {
			this.props.register(user);
		}
	}

	render() {
		const { registering  } = this.props;
		const { user, submitted } = this.state;
		return (
			<div className="body login">
				<div className="login-form">

					<Card className='form'>
						<h2>Register</h2>
						<form name="form" onSubmit={this.handleSubmit}>
							<CardContent>
								<div className={'form-group' + (submitted && !user.firstName ? ' has-error' : '')}>
									<TextField id="firstName" name="firstName" type="text" variant="outlined" label="Username"
														 onChange={this.handleChange} error={submitted && !user.firstName} fullWidth value={user.firstName}/>

									{submitted && !user.firstName &&
									<div className="help-block">First Name is required</div>
									}
								</div>
								<div className={'form-group' + (submitted && !user.lastName ? ' has-error' : '')}>
									<TextField id="lastName" name="lastName" type="text" variant="outlined" label="Last Name"
														 onChange={this.handleChange} fullWidth error={submitted && !user.lastName}  value={user.lastName}/>
									{submitted && !user.lastName &&
									<div className="help-block">Username is required</div>
									}
								</div>
								<div className={'form-group' + (submitted && !user.username ? ' has-error' : '')}>
									<TextField id="username" name="username" type="text" variant="outlined" label="Username"
														 onChange={this.handleChange} fullWidth error={submitted && !user.username} value={user.username}/>
									{submitted && !user.username &&
									<div className="help-block">Username is required</div>
									}
								</div>

								<div className={'form-group' + (submitted && !user.password ? ' has-error' : '')}>
									<TextField id="password" name="password" type="password" variant="outlined" label="Password"
														 onChange={this.handleChange} fullWidth error={submitted && !user.password} value={user.password}/>
									{submitted && !user.password &&
									<div className="help-block">Password is required</div>
									}
								</div>
							</CardContent>
							<CardActions>
								<div className="button-group">
									<Button type='submit' className="btn btn-primary action-button">Register</Button>
									{registering &&
									<img
										src="data:image/gif;base64,"/>
									}
									<Link to="/login" className="btn btn-link link-button">Cancel</Link>
								</div>
							</CardActions>
						</form>
					</Card>
				</div>
			</div>
		);
	}
}

function mapState(state) {
	const { registering } = state.registration;
	return { registering };
}

const actionCreators = {
	register: userActions.register
};

export default connect(mapState, actionCreators)(RegisterPage)